.loader_overlay {
	 width: 33vw;
	 height: 65vh;
	 background: transparent;
	 box-shadow: 0px 0px 0px 1000px rgba(255, 255, 255, 0.67), 0px 0px 19px 0px rgba(0, 0, 0, 0.16) inset;
	 border-radius: 100%;
	 z-index: -1;
	 position: absolute;
	 left: 0;
	 right: 0;
	 top: 5%;
	 bottom: 0;
	 margin: auto;
}
  .loader_cogs {
	 z-index: -2;
	 width: 33vw;
	 height: 45vh;
	 top: 0px !important;
	 position: absolute;
	 left: 0;
	 right: 0;
	 top: 0;
	 bottom: 0;
	 margin: auto;
	 border-radius: 50%;
}

.loader_cogs img{
	width: 100%;
	height: 100%;
	border-radius: 50%;
}

.loader.mob .loader_overlay, .loader.mob .loader_cogs{
	width: 90%;
}



 