footer{
	background: #222;
	padding: 40px 0;
	display: flex;
	align-items: center;
}
footer .footer-cont{
	width: 100%;
	max-width: 1100px;
	margin: 0 auto;
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	flex-wrap: wrap;
}
footer .ftr h3{
	font-size: 1.4em;
	margin-bottom: 1.2em;
	color: #fff;
}
footer .ft-content{
	list-style: none;
}
footer .ft-content a{
	text-decoration: none;
	color: #fff;
	opacity: .6;
	line-height: 40px;
	transition: .3s ease opacity;
}
.ft-content i{
	margin: 0 2vh 0 0; 
	font-size: 2.5vh;
}
footer .ft-content a:hover{
	opacity: 1;
}
footer .ftr{
	flex-basis: 20%;
}
.ftr form{
	display: flex;
	align-items: center;
	width: 100%;
}
.ftr input{
	border: none;
	outline: none;
	padding: 10px;
	margin: 5px 0;
	background: #f5f5f5;
}
.news-btn input{
	background: #f69d00;
	color: #fff;
	transition: .3s ease background ;
}
.news-btn input:hover{
	background: #000;
}
.ftr-logo{
	display: flex;
	align-items: center;
	flex-direction: column;
	width: 20vw;
	height: 15vh;
	color: #f69d00;
}
.ftr-logo img{
	width: 100%;
	height: 100%;
}
/* ------ */

/* Mobile Styling */
.mob_footer{
	
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
}
.ftr-logo.mob{
	width: 100%;
	height: 10vh;
	margin-bottom: 10vh;
}
@media only screen and (max-width: 540px){
	footer .ftr{
		flex-basis: 80%;
		margin: 0 auto;
	}
	.ftr form{
		flex-direction: column;
	}
	.ftr input{
		width: 100%;
		margin: 5px auto;
		
	}
}