.search-cont{
    position: absolute;
    width: 15%;
    height: 77%;
    background-color: #222;
}
/* -----------Search Box--------------- */
 .seacrhBox {
	 padding: 1px;
	 background: linear-gradient(-67deg, #f69d00,#f69d00);
	 overflow: hidden;
	 box-shadow: -2px -2px 6px #666, 2px 2px 2px #f69d00;
	 width: 100%;
     border-radius: 10px;
}
 .seacrhBox-inner {
	 padding: 16px 16px;
	 background-color: #e2e9f4;
}
 .seacrhBox-container {
	 display: flex;
}
 .seacrhBox-icon {
	 min-width: 46px;
	 min-height: 46px;
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 border-radius: 10px;
	 margin-right: 12px;
     font-size: 3vh;
     transition: 0.3s ease;
     cursor: pointer;
}
.seacrhBox-icon:hover{
    box-shadow: -2px -2px 6px #f69d00, 2px 2px 12px #f69d00;
}
.seacrhBox label {
	 display: block;
	 color: #3c4b66;
	 margin-bottom: 12px;
}
 .seacrhBox-inputContainer {
	 width: 100%;
}
.seacrhBox input {
	 background-color: #e3edf7;
	 padding: 16px 32px;
	 border: none;
	 display: block;
	 font-family: 'Orbitron', sans-serif;
	 font-weight: 600;
	 color: #a9b8c9;
	 -webkit-appearance: none;
	 transition: all 240ms ease-out;
	 width: 100%;
}
.seacrhBox input::placeholder {
	 color: #6d7f8f;
}
.seacrhBox input:focus {
	 outline: none;
	 color: #6d7f8f;
	 background-color: #eff5fa;
}
.seacrhBox-inputContainer {
	 --top-shadow: inset 1px 1px 3px #c5d4e3, inset 2px 2px 6px #c5d4e3;
	 --bottom-shadow: inset -2px -2px 4px rgba(255, 255, 255, .7);
	 position: relative;
	 border-radius: 1vh;
	 overflow: hidden;
}
.seacrhBox-inputContainer:before, .seacrhBox-inputContainer:after {
	 left: 0;
	 top: 0;
	 display: block;
	 content: "";
	 pointer-events: none;
	 width: 100%;
	 height: 100%;
	 position: absolute;
}
.seacrhBox-inputContainer:before {
	 box-shadow: var(--bottom-shadow);
}
.seacrhBox-inputContainer:after {
	 box-shadow: var(--top-shadow);
}
/* ------------------------------------- */
.side-item{
    width: 100%;
    height: 100%;
    padding: 1vh;
    overflow:auto;
    background-color: #222;
    user-select: none;
}
.showList{
    line-height: 3vh;
    padding: 1vh 0;
    overflow: auto;
    list-style: none;
    display: block;
}
.showList.dis{
    display: none;
}
#displayMyList:checked ~ .showList{
    display: inline;
}
.side-item ul li{
    margin: 1vh auto;
    width: 8vw;
    padding: 2vh 1vh;
    cursor: pointer;
    font-size: 2.5vh;
    color: #f8f8f8;
}
.side-item ul li:hover{
    background-color: #f69d00;
}
.side-item.active{
    background-color: #f69d00;

}
.side-item h3{
    font-size: 3vh;
    padding: 1vh 3vh;
    line-height: 6vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    color: #f69d00;
}
/* ---------------------------------- */
.shop-body{
    position: absolute;
    width: 85%;
    height: 90%;
    top: 10%;
    left: 15%;
    background-color: #f9f9f9;
    overflow: auto;
}
.shop-body h1{
    text-align: center;
    font-size: 6vh;
    color: rgb(0, 0, 0);
    margin: 0 35% 2vh;
    border-bottom: 3px solid #f69d00;
}
.card-holder{
    display: grid;
    grid-template-columns: repeat(3,0fr);
    justify-content: space-evenly;
}
.card-product{
    width: 22vw;
    height: 55vh;
    box-shadow: -2px -2px 6px #f69d00, 2px 2px 2px #f69d00;
    margin: 1vh;
    line-height: 4vh;
    background: #cacaca;
}
.category{
    text-align: center;
    line-height: 6vh;
}
.aligning{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.card-product img{
    width: calc(100% - 20%);
    height: 40%;
    margin: 0 10%;
    object-fit: contain;
}
.card-product h3 img{
    width: 6vw;
    height: 4vh;
}
.category h3{
    display: inline-flex;
    align-items: center;
    color: #333;
    user-select: none;
}
.category h3 span{
    color: #000;
    margin: 0 1vh;
}
.card-product h3 input{
    width: 4vw;
    margin: 1vh;
    color: #000;
    font-weight: bold;
    font-size: 1.8vh;
}
.card-product h3 i{
    font-size: 3vh;
    margin: 0 0 0 1vh;
}
.category.cart{
    border-bottom: 3px groove #fff;
    cursor: pointer;
}
.category.cart:hover{
    color: #000;
}
.card-head{
    display: flex;
    justify-content: center;
    align-items: center;
}
.card-head h2{
    color: #000;
    margin: 1vh 0;
    font-size: 3vh;
}
.card-head i{
    position: relative;
    left: -30%;
    font-size: 2.5vh;
    cursor: pointer;
    color: #f69c0052;
}
.card-head i:hover{
    color: #f69d00;
}
.measurments{
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(1vh);
}
.measurments label{
    font-size: 2.2vh;
    color: white;
}
.measurments span{
    margin: 0 1vh;
    transform: translateY(1vh);
    color: white;
}
.measurments input[type=number]{
    width: 7vh;
    height: 4vh;
    outline: #f69d00;
    border-color: #f69d00;
    border-radius: 1vh;
    text-align: center;
}
/* ------------Modal Style--------------------- */
.modal-section i{
    position: fixed;
    right: 2%;
    top: 12%;
    font-size: 4vh;
    cursor: pointer;
    z-index: 2;
    color: #f69d00;
    transition: 0.2s ease;
}
.modal-section i:hover{
    color: black;
}
#closeModal{
    display: none;
}
.modal-btn{
    display: none;
}
.modal{
  position: fixed;
  display: block;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-pack: center;
  justify-content: center;
  margin: 0 auto;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  overflow-x: hidden;
  background-color: rgba(31,32,41,.75);
  pointer-events: none;
  opacity: 0;
  transition: opacity 250ms 700ms ease;
}
.modal-btn:checked ~ .modal{
  pointer-events: auto;
  opacity: 1;
  transition: all 300ms ease-in-out;
}
.modal-btn:checked ~ #closeModal{
    display: block;
}
.modal-wrap {
  position: relative;
  display: block;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 4px;
  overflow: hidden;
  padding-bottom: 20px;
  background-color: #fff;
    -ms-flex-item-align: center;
    align-self: center;
    box-shadow: 0 12px 25px 0 rgba(199,175,189,.25);
  opacity: 0;
  transform: scale(0.6);
  transition: opacity 250ms 250ms ease, transform 300ms 250ms ease;
}
.modal-wrap img {
  display: block;
  width: 100%;
  height: auto;
}
.modal-wrap p {
  padding: 20px 30px 0 30px;
}
.modal-btn:checked ~ .modal .modal-wrap,
.modal-btn:checked ~ .modal .modalInputs
{
  opacity: 1;
  transform: scale(1);
  transition: opacity 250ms 500ms ease, transform 350ms 500ms ease;
}
.modalInputs{
    position: relative;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    width: 60vh;
    height: 50vh;
    margin: 0 auto;
    margin-top: 2vh;
    margin-bottom: 2vh;
    border-radius: .5vh;
    overflow: hidden;
    padding-bottom: 2vh;
    background-color: #fff;
    -ms-flex-item-align: center;
    align-self: center;
    box-shadow: 0 1vh 3vh 0 rgba(199,175,189,.25);
    opacity: 0;
    transform: scale(0.6);
    transition: opacity 250ms 250ms ease, transform 300ms 250ms ease;
}
.modalInputs h2{
    font-size: 5vh;
    border-bottom: 3px groove #f69d00;
}
.modalInputs div{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 2.5vh;
}
.modalInputs i{
    position: absolute;
    top: 3%;
    color: #f69d00;
}
.modalInputs input{
    width: 140%;
    height: 4vh;
    font-size: 2vh;
    outline: #f69d00;
    border: none;
    border-bottom: 1px solid #666;
}
.modalInputs label{
    letter-spacing: .4vh;
    margin: 1vh 0;
    font-weight: 600;
    color: #222;
}
.modalInputs button{
    width: 20vh;
    height: 5vh;
    border-radius: 2vh;
    outline: none;
    border: none;
    cursor: pointer;
    background: linear-gradient(147deg,#8a5802 0%, #f69d00 74%);
    opacity: 0.8;
    font-size: 3vh;
    color: #000;;
}
.modalInputs button:hover{
    opacity: 1;
}
/* ---------Cart Styles--------------- */
.product-image {
    float: left;
    width: 20%;
}
.product-details {
    float: left;
    width: 37%;
}
.product-price {
    float: left;
    width: 12%;
}
.product-quantity {
    float: left;
    width: 10%;
}
.product-removal {
    float: left;
    width: 9%;
}
.product-line-price {
    float: left;
    width: 12%;
    text-align: right;
}
.group:before, .shopping-cart:before, .column-labels:before, .product:before, .totals-item:before, .group:after, .shopping-cart:after, .column-labels:after, .product:after, .totals-item:after {
    content: '';
    display: table;
}
.group:after, .shopping-cart:after, .column-labels:after, .product:after, .totals-item:after {
    clear: both;
}
.group, .shopping-cart, .column-labels, .product, .totals-item {
    zoom: 1;
}
.product .product-price:before, .product .product-line-price:before, .totals-value:before {
    content: '$';
}
.shopping-cart {
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 4vh;
}
.column-labels label {
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: 1px solid #eee;
}
.column-labels .product-image, .column-labels .product-details, .column-labels .product-removal {
    text-indent: -9999px;
}
.product {
    margin-bottom: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
}
.product .product-image {
    text-align: center;
}
.product .product-image img {
    width: 15vh;
    height: 8vh;
}
.product .product-details .product-description {
    margin: 5px 20px 5px 0;
    line-height: 1.4em;
}
.product .product-quantity input {
    width: 7vh;
}
.product .remove-product {
    border: 0;
    padding: 4px 8px;
    background-color: #c66;
    color: #fff;
    font-family: 'HelveticaNeue-Medium', 'Helvetica Neue Medium';
    font-size: 12px;
    border-radius: 3px;
    cursor:pointer;
}
.product .remove-product:hover {
    background-color: #a44;
}
.totals .totals-item {
    float: right;
    clear: both;
    width: 100%;
    margin-bottom: 10px;
}
.totals .totals-item label {
    float: left;
    clear: both;
    width: 79%;
    text-align: right;
}
.totals .totals-item .totals-value {
    float: right;
    width: 21%;
    text-align: right;
}
.totals .totals-item-total {
    font-family: 'HelveticaNeue-Medium', 'Helvetica Neue Medium';
}
.checkout {
    float: right;
    border: 0;
    margin-top: 20px;
    padding: 6px 25px;
    background-color: #6b6;
    color: #fff;
    font-size: 25px;
    border-radius: 3px;
    cursor: pointer;
}
.checkout:hover {
    background-color: #494;
}
