*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.homeBody{
    width: 100%;
    height: 100%;
}
.brands{
    width: 100%;
    height: 100%;
}
.brands h1{
    font-size: 10vh;
    margin: 6vh 30%;
    border-bottom: 3px groove #666;
    text-align: center;
    color: black;
}
.brands h4{
    font-size: 4vh;
}
.brands div:nth-child(1){
    display: flex;
    justify-content: center;
}
.brands select{
    width: 15vw;
    text-align: center;
    margin: 3vh;
    padding: 1vh;
    font-size: 3vh;
    border-radius: 5%;
    border: none;
    outline: none;
    border-bottom:4px solid #b7b7b7;
    background-color: #b7b7b7;
}
.brandsHolder{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1vh 5vh;
    margin: 5vh 10% 20vh;
    letter-spacing: 0.5vh;
    border-radius: 2vh;
    background-color: #fff;
    box-shadow: 0 1vh 3vh rgba(0,0,0,0.12), 0 1vh 2vh rgba(0,0,0,0.24);
}
.brandsHolder div:nth-child(1){
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.brandsHolder div img{
    width: 30vh;
    height: 15vh;
    object-fit: contain;
    margin: 0 0 5vh 0;
}
.brandsHolder img{
    width: 50vh;
    height: 50vh;
    border-radius: 2vh;
    /* animation: brands 2s ease-in infinite; */

}
.brandsHolder h2{
    font-size: 3vh;
    text-transform: uppercase;
    /* animation: brands 2s ease-in infinite; */

}
@keyframes brands {
    0%{
        opacity: 0;;
    }
    100%{
        opacity: 1;
    }
}
.brandsHolder i{
    font-size: 10vh;
    color: #f69c005e;
    cursor: pointer;
}
.brandsHolder i:hover{
    color: #f69d00;
}
.miss-card{
    width: calc(100% - 20%);
    height: 100%;
    margin: 0 auto 10vh;
    box-shadow: 0 10px 50px rgba(0, 0, 0, 0.3);
    padding: 4vh;
    border-radius: 2vh;
    line-height: 6vh;
    letter-spacing: .1em;
    font-size: 1.3em;
}
/* Mobile Styling */
.brands.mob h1{
    font-size: 2.5rem;
    margin: 2vh 5% ;
}
.brands.mob .brandsHolder{
    flex-direction: column;
    width: 95%;
    margin: 5vh auto 10vh;

}
.brands.mob .brandsHolder h2{
    font-size: 2em;
    margin-bottom: 5vh;
}
.brands.mob .brandsHolder img{
    height: 30vh;
    width: 90vw;
}
.brands.mob .brandsHolder div img{
    width: 90vw;
    height: 10vh;
}
.brands.mob .miss-card{
    width: calc(100% - 5%);
    margin: 0 auto 10vh;
    padding: 2vh 1vh;
    line-height: 6vh;
    letter-spacing: 0;
    font-size: 1em;
}
