.sliderbody{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh;
    width: 100%;
    background: linear-gradient(147deg,#8a5802 0%, #f69d00 74%);
}
.blog-card{
    position: absolute;
    height: 80vh;
    width: 100%;
    max-width: 70vw;
    margin: auto;
    background: rgb(255, 255, 255);
    border-radius: 3vh;
    box-shadow: 0 10px 50px rgba(0, 0, 0, 0.3);
}
.blog-card .inner-part{
    position: absolute;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: start;
    padding: 5vh;
    height: 60%;
    width: 100%;
    top: 0;
}
.inner-part img{
    height: 90%;
    width: 100%;
    overflow: hidden;
    margin: auto;
    flex-shrink: 0;
    border-radius: 2vh;
    box-shadow: 2px 3px 15px rgba(0, 0, 0, 0.2);
    transition: .6s;
}
.content{
    padding: 2vh 4vh;
    width: 95%;
    height: 45vh;
    opacity: 0;
    transition: 0.6s;
}
.inner-part .activecontent{
    opacity: 1;
    margin-left: 0px;
    z-index: 1;
    transition-delay: 0.3s;
}
.content span{
    display: block;
    color: #f69d00;
    margin-bottom: 2vh;
    font-size: 2.3em;
    font-weight: bold;
}
.content .title{
    font-size: 3em;
    font-weight: 700;
    color: #000000;
    margin-bottom: 2vh;
}
.content .text{
    font-size: 1.4em;
    color: #4e4a67;
    margin-bottom: 3vh;
    line-height: 5vh;
    letter-spacing: .1em;
    text-align: justify;
}
.content button{
    display: inline-flex;
    padding:  1.5vh 3vh;
    border: none;
    font-size: 2vh;
    text-transform: uppercase;
    color: #fff056;
    font-weight: 600;
    letter-spacing: .1vh;
    border-radius: 5vh;
    cursor: pointer;
    background: linear-gradient(147deg,#8a5802 0%, #f69d00 74%);
}
.sliders label{
    position: absolute;
    top: 75vh;
    left: 85%;
    z-index: 1;
}
.sliders .tap{
    position: relative;
    height: 1vh;
    width: 5vh;
    background: #d9d9d9;
    border-radius: 1vh;
    display: inline-flex;
    margin: 0 .5vh;
    cursor: pointer;
    z-index: 2;
}
.sliders .tap::before{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    height: 100%;
    width: -100%;
    background: linear-gradient(147deg,#8a5802 0%, #f69d00 74%);
    border-radius: 1vh;
    transform: scaleX(0);
    transition: transform 0.6s;
    transform-origin: left;
}
.tap.active-tap::before{
    transform: scaleX(1);
    width: 100%;
}
.blog-card input{
    display: none;
}
/* Mobile Styling */
.blog-card.mob{
    max-width: 100vw;
    height: 85vh;
}
.blog-card.mob .inner-part img{
    height: 70%;
}
.blog-card.mob .content{
    width: 100%;
    padding: 1vh 0;
}
.blog-card.mob .content span{
    font-size: 1.7em;
}
.blog-card.mob .content .title{
    font-size: 2em;
}
.blog-card.mob .content .text{
    font-size: 1em;
    line-height: 4vh;
    letter-spacing: 0;
}
.blog-card.mob .sliders label{
    top: 81vh;
    left: 35%;
}