.clients-body{
    width: 100%;
    height: 100%;
    /* background: linear-gradient(147deg,#8a5802 0%, #f69d00 74%); */
    background-color: #f8f8f8;
}
.clients-body h1{
    text-align: center;
    padding: 4vh 0;
    font-size: 8vh;
    color: #f69d00;
}
.clients-slider{
    display: grid;
    grid-template-columns: repeat(3,0fr);
    justify-content: space-around;
    width: 95%;
    margin: 0 auto 6vh;
    padding: 4vh 2vh;
    background-color: #fff;
    box-shadow: 0 1vh 6vh rgba(0, 0, 0, 0.3);
    border-radius: 2vh;
}
.imgSlide{
    width: 20vw;
    height: 15vh;
    margin: 3vh 0;
}
/* Mobile Styling */
.clients-body.mob h1{
    font-size:2em;
}
.clients-slider.mob{
    grid-template-columns: repeat(2,0fr);
    padding: 2vh;
}
.clients-slider.mob .imgSlide{
    width: 40vw;
    height: 10vh;
    margin: 4vh 5vw;
}