.cards-sect{
    position: relative;
    width: 100%;
    height: 110vh;
    padding-top: 3vh;
}
.cards-sect h2{
    text-align: center;
    font-size: 5rem;
    margin-bottom: 4vh;
    letter-spacing: 0.4vh;
    color: rgb(255, 115, 0);
}
.sectors{
    position: relative;
    display: grid;
    grid-template-columns: repeat(3,0fr);
    justify-content: center;
    box-shadow: -.2vh -.2vh 3vh rgb(255, 115, 0), .2vh .2vh 3vh rgb(255, 115, 0);
    width: 100%;
    border-radius: 2vh;
    width: 96%;
    margin: 0 auto;
}
.sectors section{
    width: 32vw;
    height: 45vh;
    font-size: 2vh;
}
.sectors section:nth-child(even){
      background-color: rgb(66, 66, 66);
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      line-height: 4vh;
      padding: 2vh;
}
.sectors i{
    font-size: 6vh;
    color: rgb(255, 255, 255);
    margin: 0 2vh;
}
.sectors section:nth-child(even) h1{
    font-size: 4vh;
    margin: 2vh 0;
}
.sectors section:nth-child(even) p{
    color: rgb(252, 252, 252);
}
.sectors section:nth-child(1){
      background: url(../assets/jscM.jpg) no-repeat center;
      background-size: 100% 100%;
      border-radius: 2vh 0 0 0;
}
.sectors section:nth-child(3){
    background: url(../assets/tim.jpg) no-repeat;
    background-size: 100% 100%;
    border-radius: 0 2vh 0 0;
}
.sectors section:nth-child(5){
    background: url(../assets/logos/logo.jpg) no-repeat center;
    background-size: 100% 100%;
}
.sectors section:nth-child(4){
      border-radius: 0 0 0 2vh;

}
.sectors section:nth-child(6){
      border-radius: 0 0 2vh 0;

}
/* Mobile Styling */
.cards-sect.mob{
    width: 95%;
    height: 100%;
    margin: 0 auto;
    padding: 2vh 0;
}
.cards-sect.mob .sectors{
    grid-template-columns: repeat(1,0fr);
}
.cards-sect.mob .sectors section{
    width: 95vw;
    border-radius: 1vh;
}
.cards-sect.mob .sectors section:nth-child(odd){
    height: 30vh;
}
.cards-sect.mob .sectors section div{
    display: flex;
    align-items: center;
}

