.menu-bar{
    background-color: #222;
    text-align: center;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.menu-bar img{
    width: 15vw;
    height: 10vh;
}
.menu-bar ul{
    display: inline-flex;
    list-style: none;
    color: #fff;
}
.menu-bar ul li{
    width: 150px;
    margin: 15px;
    padding: 10px;
    cursor: pointer;
}
.menu-bar ul li a{
    text-decoration: none;
    color: #fff;
    font-size: 2vh;
    display: inline-flex;
}
.active, .menu-bar ul li:hover{
    background: #f69d00;
    border-radius: 3px;
}
.menu-bar i{
    margin-right: 8px;
}
.sub-menu-1{
    display: none;
    z-index: 2;
}
.menu-bar ul li:hover .sub-menu-1{
    display: block;
    position:absolute;
    background: #222;
    margin-top: 15px;
    margin-left: -15px;
}

.menu-bar ul li:hover .sub-menu-1 ul{
    display: block;
    margin: 10px;
}
.menu-bar ul li:hover .sub-menu-1 ul li{
    width: 150px;
    padding: 10px;
    border-bottom: 1px groove #fff;
    background: transparent;
    border-radius: 0;
    text-align: left;
}
.menu-bar ul li:hover .sub-menu-1 ul li:last-child{
    border-bottom: none;
}
.menu-bar ul li:hover .sub-menu-1 ul li a:hover{
    color: #f69d00;
}
.hover-it i{
    float: right;
}
.sub-menu-2{
    display: none;
}
.hover-it:hover .sub-menu-2{
    position: absolute;
    display: block;
    margin-left: 140px;
    background: #222;
}
/* Mobile Styling */
.menu-bar.mob img{
    display: none;
}
.menu-bar.mob ul li{
    width: 11vw;
    padding: 0;
    margin: 2vh 5vw 2vh 3.5vw;
}
.menu-bar.mob ul li a{
    font-size: .8em;
}
.menu-bar.mob i{
    margin-right: 1vw;
}