.contact-body {
    display: flex;
    height: 90vh;
    background: linear-gradient(147deg,#8a5802 0%, #f69d00 74%);
}
.contact-container {
    flex: 0 1 100vh;
    margin: auto;
    padding: 2vh;
}
.screen {
    position: relative;
    background: rgba(0, 0, 0, 0.699);
    border-radius: 2vh;
    height: 55vh;
}
.screen-header {
    display: flex;
    align-items: center;
    padding: 1vh 2vh;
    background: #4d4d4f79;
    border-top-left-radius: 1.5vh;
    border-top-right-radius: 1.5vh;
}
.screen-body {
    display: flex;
}
.screen-body-item {
    flex: 1;
    padding: 5vh;
}
.screen-body-item.left {
    display: flex;
    flex-direction: column;
    
}
.screen-body-item.left span{
    color: rgb(0, 0, 0);
    font-size: 4vh;
}
.app-title {
    display: flex;
    flex-direction: column;
    position: relative;
    color: #f69d00;
    font-size: 2vh;
    font-weight: bold;
    text-transform: uppercase;
}
.screen-body-item.left span:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: -1vh;
    width: 85%;
    height: .5vh;
    background: #f69d00;
}  
.app-contact {
    margin-top: auto;
    font-size: 2vh;
    color: #fff;
    display: inline-flex;
    justify-content: space-between;
    align-items: flex-start;
}
.app-contact i{
    font-size: 3vh;
    margin: 0 1vh;
}
.app-form-group {
    margin-bottom: 3vh;
}
.app-form-group.message {
    margin-top: 5vh; 
}
.app-form-group.buttons {
    margin-bottom: 0;
    text-align: right;
}
.app-form-control {
    width: 100%;
    padding: 1vh 0;
    background: none;
    border: none;
    border-bottom: .1vh solid #fff;
    color: #fff;
    font-size: 2vh;
    text-transform: uppercase;
    outline: none;
    transition: border-color .2s;
}
.app-form-control::placeholder {
    color: #666;
}
.app-form-control:focus {
    border-bottom-color: #fff;
}
.app-form-button {
    background: none;
    border: none;
    color: #fff;
    font-size: 2vh;
    cursor: pointer;
    outline: none;
    margin: 1vh;
}
.app-form-button:hover {
    color: #f69d00;
}
.screen-header-right {
    display: flex;
}
.screen-header-ellipsis {
    width: 3px;
    height: 3px;
    margin-left: 2px;
    border-radius: 8px;
    background: #999;
}

@media screen and (max-width: 600px) {
    .contact-body{
        height: 100vh;
    }
    .screen-body {
      padding: 3vh;
    }
  
    .screen-body-item {
      padding: 0;
    }
}
/* Mobile Styling */

.screen.mob{
    height: 100%;
}
.screen.mob .screen-body{
    flex-direction: column;
}
.screen.mob .app-contact{
    margin: 1vh 0;
}
.screen.mob .screen-body-item.left{
    line-height: 7vh;
}
  